import anime from 'animejs/lib/anime.es'

var lastScrollTop = 0;
const scrollingAnimation = (cubes:HTMLCollectionOf<Element>) => {
    const boxShrinker = document.getElementById('box-shrinker')
    if(!boxShrinker)
        return
    window.addEventListener('scroll',(e)=>{
        var scrollingTop = window.pageYOffset || document.documentElement.scrollTop; // Credits: "https://github.com/qeremy/so/blob/master/so.dom.js#L426"
        const scrollingUp = scrollingTop > lastScrollTop
        cubesChanging(cubes)
        decomposition(cubes, scrollingUp)
        lastScrollTop = scrollingTop <= 0 ? 0 : scrollingTop
        for (let i=0; i<cubes.length;i++) {
            const cube = cubes[i] as HTMLElement
            if (fixedCross(cube,boxShrinker)){
                const sides = cube.getElementsByClassName('side')
                for (let ii = 0; ii < sides.length; ii++) {
                    const side = sides[ii] as HTMLElement
                    side.style.transition = 'all 0.2s'
                    side.style.borderRadius = '25px'
                    side.style.borderWidth = '1px'
                    side.style.transition = '3s'
                }
            }
        }
    }) 
    // const observers
    // registerSectionsObservers(cubes)
}
const fixedCross = (fixed :  HTMLElement, toCross: HTMLElement) => {
    var fixed = fixed;
  
    var fixed_position = fixed.getBoundingClientRect().top
    var fixed_height =  fixed.offsetHeight
  
    var toCross_position = toCross.getBoundingClientRect().top;
    // var toCross_height = toCross.offsetHeight;
    
    if (fixed_position +fixed_height < toCross_position) {
      return false
    } else if (fixed_position +fixed_height > toCross_position ) {
      return true
    } else {
        return false
    }
}
const decomposition = (cubes,scrollingUp) => {
    const toDecompose = offsetIsTopHalf()
    let percentageOffset = getPercentageOffset()
    for (let i = 0; i < cubes.length ; i ++) {
        const cube = cubes[i]
        const rollAnim = rollAnimation(cube,scrollingUp )
        // const rollAnim = decomposeAnimation(cube, scrollingUp)
        if(percentageOffset < 15 || percentageOffset > 85)
            percentageOffset = 0
        rollAnim.seek(rollAnim.duration * (percentageOffset / 100));
    }
    if(toDecompose === true) {
        
    } else {

    }
}

const getPercentageOffset = () => {
    const navBar = document.querySelector('nav')
    const extraHeight = 60 
    let currentYPos = window.pageYOffset
    const relativeOffset = currentYPos > window.innerHeight? currentYPos - window.innerHeight : currentYPos
    return (relativeOffset ) / window.innerHeight * 100
}
const offsetIsTopHalf = () => {
    // let currentYPos = window.pageYOffset
    // const relativeOffset = currentYPos > window.innerHeight? currentYPos - window.innerHeight : currentYPos
    return getPercentageOffset() < 50
}
const cubesChanging = (cubes)=> {
    const texts = [['Input', 'Output'],['Idea','Product'], ['Info', 'Tech'], ['Data', 'Action'], ['Art', 'Code'], ['Fake', 'News']]

    for (let i =0 ; i < cubes.length; i++) {
        const cube = cubes[i] as HTMLElement
        const sections = document.getElementsByClassName('section')
        for (let ii = 0; ii < sections.length; ii++ ) {
            const section = sections[ii]
            const text = texts[ii][i] || texts[0].join(' ')
            // registerSectionObserver(section, cubes, text)
            const collide = areColliding(section, cube)
            if(collide) {
                const front = cube.getElementsByClassName('front')[0] as HTMLElement
                front.innerHTML = text
            }
        }
    }
}


/**
 * Thanks to https://gist.github.com/jtsternberg/c272d7de5b967cec2d3d
 * Detects if two elements are colliding
 *
 * Credit goes to BC on Stack Overflow, cleaned up a little bit
 *
 * @link http://stackoverflow.com/questions/5419134/how-to-detect-if-two-divs-touch-with-jquery
 * @param $div1
 * @param $div2
 * @returns {boolean}
 */
var areColliding = function( el1, el2 ) {
    var rect1 = el1.getBoundingClientRect();
    var rect2 = el2.getBoundingClientRect();

    return !(
      rect1.top > rect2.bottom ||
      rect1.right < rect2.left ||
      rect1.bottom < rect2.top ||
      rect1.left > rect2.right
    );
};
export default scrollingAnimation




