import anime from 'animejs/lib/anime.es';
import {getRandomColor} from '../utils/colors'
import scrollingAnimation from './scrollingAnimation';
const setupCubes = () => {
    const cubes = document.getElementsByClassName('cube')
    const cubeContainers = document.getElementsByClassName('cube-container')
    for(let i = 0; i < cubes.length; i++) {
        const cube = cubes[i] as HTMLElement
        shuffleBox(cube)
        cube.addEventListener('click',()=>{
            shuffleBox(cube)

        })
    }
    // for(let i = 0; i < cubeContainers.length; i++) {
    //     const cubeContainer = cubeContainers[i] as HTMLElement
    //     shuffleBox(cubeContainer)
    //     cubeContainer.addEventListener('click',()=>{
    //         shuffleBox(cubeContainer)
    //     })
    // }
    scrollingAnimation(cubes)

}

const cubeBaseSideTranslate = deepFreeze({
    front: {
        translateZ: 55,
        rotateX:0
    },
    
    back: {
        translateZ: -55, 
        rotateY: 180
    },
    
    top: {
        translateY: -55, 
        rotateX: 90
    },
    
    bottom: {
        translateY: 55, 
        rotateX: -90
    },
        
    
    left: {
        translateX: -55, 
        rotateY: -90
    },
    
    right: {
        translateX: 255, 
        rotateY: 90
    } 
})

const getTransformStringValues = (transformString:string) => {
    const translateX = transformString.match(/translateX\((([0-9]|[a-z])+)\)/)[1] || null
    const translateY = transformString.match(/translateY\((([0-9]|[a-z])+)\)/)[1] || null
    const translateZ = transformString.match(/translateZ\((([0-9]|[a-z])+)\)/)[1] || null
    const rotateX = transformString.match(/rotateX\((([0-9]|[a-z])+)\)/)[1] || null
    const rotateY = transformString.match(/rotateY\((([0-9]|[a-z])+)\)/)[1] || null
    const rotateZ = transformString.match(/rotateZ\((([0-9]|[a-z])+)\)/)[1] || null
    return  {
        translateX,
        translateY,
        translateZ,
        rotateX,
        rotateY,
        rotateZ,
    }
}
const shuffleBox = (cube:HTMLElement) => {
 
    const shuffleRotations = (o) => {
        const oc = JSON.parse(JSON.stringify(o))

        const keys = Object.keys(oc)
        keys.forEach(key=>{
            const val = oc[key]
            const valKeys = Object.keys(val)
            valKeys.forEach(valKey=>{
                let increasor = Math.random() * 1800 - 150
                oc[key][valKey] =   increasor
            })
            // if(valKeys.length < 2) {
            //     oc[key]['rotateZ'] =    Math.random() * 1800 - 150
            // }
        })
        return oc
    }
    


    // for (let i = 0 ; i < cubes.length ; i ++) {
    //     const cube = cubes[i]
        const sides = cube.getElementsByClassName('side')
        const shufflePosCopy = shuffleRotations({...cubeBaseSideTranslate})
        for (let ii = 0 ; ii < sides.length ; ii ++) {
            const side = sides[ii]
            const position = side.classList[1]
            
            const getCustom = () => {
                if(position === 'front') {
                    return {
                        background:getRandomColor(),
                        color: 'white'
                    }
                }
                return {}
            }
            anime({
                targets: side,
                // translateX: 250,
                // rotateX: '1turn',
                ...shufflePosCopy[position],
                ...getCustom(),
                // backgroundColor: '#FFF',
                duration: 2200,
                easing: 'easeInOutQuad',
                // easing: 'linear',
                complete: function(anim) {

                    window.addEventListener('scroll',function recover(){
                        anime({
                            targets: side,
                            // translateX: 250,
                            // rotateX: '1turn',
    
                            ...cubeBaseSideTranslate[position],
    
                            // backgroundColor: '#FFF',
                            duration: 500
                        });
                        window.removeEventListener('scroll',recover)
                    })
                    
                }
            });
          
        }
    // }

}





function deepFreeze (o) {
    Object.freeze(o);
    if (o === undefined) {
      return o;
    }
  
    Object.getOwnPropertyNames(o).forEach(function (prop) {
      if (o[prop] !== null
      && (typeof o[prop] === "object" || typeof o[prop] === "function")
      && !Object.isFrozen(o[prop])) {
        deepFreeze(o[prop]);
      }
    });
  
    return o;
  };



export default setupCubes
