
const populateShapes = () =>{
    // Thank you to https://codepen.io/32kB/pen/rdYoGV
    var html = '';
    for (var i = 1; i <= 50; i ++) {
        html += '<div class="shape-container-'+i+' shape-animation"><div class="random-shape"></div></div>';
    }
    
    // document.querySelector('.shape').innerHTML += html;
    const shapeStripes = document.getElementsByClassName('shape-stripe')
    for(let i = 0; i < shapeStripes.length; i++) {
        const shapeStripe = shapeStripes[i] 
        shapeStripe.innerHTML += html;
    }

    
}

export default populateShapes